.contactHeader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 50px;
  margin-top: 0;
}

.contactHeader p {
  font-weight: 500;
  color: gray;
}

.contactHeader .details {
  text-align: start;
  margin-left: 15px;
}

@media (max-width: 992px) {
  .contactHeader {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    padding-left: 5px;
    padding-right: 20px;
  }
}
